"use client";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { useToast } from "@/hooks/use-toast";
import { z } from "zod";
import useAuth from "@/hooks/useAuth";
import { Button } from "@/components/ui/button";
import {
    Form,
    FormControl,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { motion, AnimatePresence } from "framer-motion";
import { Building2, Mail, Phone, MapPin, Link, FileText, ArrowRight, CircleDashed } from "lucide-react";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { cn } from "@/lib/utils";
import { useRouter } from "next/navigation";
import { useState, useEffect } from "react";
import { mockHostCategories } from "@/constants/mockHostCategories";
const organizerSchema = z.object({
    organizerType: z.enum(["company", "university"]),
    companyName: z.string().min(3, "Name must contain at least 3 characters"),
    contactPerson: z.string().min(3, "Name must contain at least 3 characters"),
    email: z.string().email("Invalid email address"),
    phone: z.string().regex(/^\d{10}$/, "Phone must be 10 digits"),
    address: z.string().min(5, "Address must contain at least 5 characters"),
    website: z.string().url("Invalid website URL"),
    document: z.instanceof(File, { message: "Authorization document is required" })
});

const OrganizerRegisterForm = () => {
    const { toast } = useToast();
    const router = useRouter();
    const categories = mockHostCategories;
    const { checkEmailExists, emailCheckLoading } = useAuth()
    const [isLoading, setIsLoading] = useState(true);
    const [loading, setLoading] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);

    // Ø§Ø³ØªØ®Ø±Ø§Ø¬ Ø±Ø§Ø¨Ø· API Ù…Ù† Ø§Ù„Ù…ØªØºÙŠØ±Ø§Øª Ø§Ù„Ø¨ÙŠØ¦ÙŠØ©
    const API_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:3001";

    console.log(`api url => ${API_URL}`)
    const form = useForm<z.infer<typeof organizerSchema>>({
        resolver: zodResolver(organizerSchema),
        defaultValues: {
            organizerType: "company",
            companyName: "",
            contactPerson: "",
            email: "",
            phone: "",
            address: "",
            website: "",
        },
    });
    useEffect(() => {
        const fetchCategories = async () => {
            try {
                const response = await fetch(`${process.env.API_URL}/api/admin/host-categories`, {
                    method: "GET",
                    credentials: "include",
                });
                const data = await response.json();
                if (!response.ok) {
                    toast({
                        title: "Error fetching host categories",
                        description: data.message || "Failed to load events",
                        variant: "destructive",
                    });
                }
                // setCategories(data);
            } catch (error) {
                console.error('Error fetching host categories:', error);
                toast({
                    title: "Error fetching",
                    description: `${error}`,
                    variant: "destructive",
                });
            } finally {
                setLoading(false);
            }
        };

        fetchCategories();

    }, []);
    useEffect(() => {
        const timer = setTimeout(() => {
            setIsLoading(false);
        }, 1000);

        return () => clearTimeout(timer);
    }, []);

    const onSubmit = async (values: z.infer<typeof organizerSchema>) => {
        setIsSubmitting(true);

        try {
            // Ø§Ù„ØªØ­Ù‚Ù‚ Ù…Ù† Ø§Ù„Ø¨Ø±ÙŠØ¯ Ù…Ø¹ Ø§Ù„Ø¯ÙˆØ± Ø§Ù„Ù…Ø¶ÙŠÙ
            const emailExists = await checkEmailExists(values.email, "host");

            if (emailExists) {
                toast({
                    title: "Email Already Registered",
                    description: "This email is already associated with a host account.",
                    variant: "destructive",
                    duration: 5000
                });
                return;
            }

            const formData = new FormData();
            formData.append('organizerType', values.organizerType);
            formData.append('companyName', values.companyName);
            formData.append('contactPerson', values.contactPerson);
            formData.append('email', values.email);
            formData.append('phone', values.phone);
            formData.append('address', values.address);
            formData.append('website', values.website);
            formData.append('document', values.document);
            formData.append('role', 'host');

            const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/register/host`, {
                method: "POST",
                body: formData,
                credentials: "include",
            });

            if (response.ok) {
                const data = await response.json();

                toast({
                    title: "ðŸŽ‰ Registration Successful!",
                    description: "Your host account has been created successfully!",
                    className: "bg-emerald-600 text-white border-0",
                });

                setTimeout(() => {
                    router.push("/host/dashboard");
                }, 1000);
            } else {
                const errorData = await response.json();
                throw new Error(errorData.message || "Registration failed");
            }
        } catch (error: any) {
            toast({
                title: "Registration Failed",
                description: error.message || "An error occurred during registration",
                variant: "destructive",
                duration: 5000
            });
        } finally {
            setIsSubmitting(false);
        }
    };

    if (isLoading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-indigo-50 to-blue-100">
                <motion.div
                    animate={{ rotate: 360 }}
                    transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                    className="flex flex-col items-center"
                >
                    <CircleDashed className="h-16 w-16 text-blue-600" />
                    <p className="mt-4 text-lg font-medium text-gray-700">Loading registration form...</p>
                </motion.div>
            </div>
        );
    }
    return (
        <div className="min-h-screen bg-gradient-to-br from-indigo-50 to-blue-100 p-4 md:p-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, ease: "easeOut" }}
                className="max-w-4xl w-full mx-auto bg-white rounded-2xl shadow-xl overflow-hidden"
            >
                <div className="relative">
                    {/* Form container */}
                    <div className="relative z-10 p-8 md:p-12">
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ delay: 0.2 }}
                            className="text-center mb-12"
                        >
                            <motion.div
                                whileHover={{ scale: 1.05 }}
                                whileTap={{ scale: 0.95 }}
                                className="inline-block mb-6"
                            >
                                <Building2 className="mx-auto h-16 w-16 text-blue-600 p-3 bg-blue-50 rounded-full" />
                            </motion.div>
                            <motion.h1
                                initial={{ y: -10 }}
                                animate={{ y: 0 }}
                                transition={{ delay: 0.3 }}
                                className="text-3xl md:text-4xl font-bold text-gray-900 mb-2"
                            >
                                Host Registration
                            </motion.h1>
                            <motion.p
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                transition={{ delay: 0.4 }}
                                className="text-gray-600 text-lg"
                            >
                                For universities, companies, and certified institutions
                            </motion.p>
                        </motion.div>

                        <Form {...form}>
                            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
                                <AnimatePresence mode="wait">
                                    <motion.div key={form.watch("organizerType")}
                                        initial={{ opacity: 0, x: -10 }}
                                        animate={{ opacity: 1, x: 0 }}
                                        exit={{ opacity: 0, x: 10 }}
                                        transition={{ duration: 0.3 }}
                                    >
                                        <FormField
                                            control={form.control}
                                            name="organizerType"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel className="text-gray-700 font-medium">Organization Type</FormLabel>
                                                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                                                        <FormControl>
                                                            <SelectTrigger className="h-12">
                                                                <SelectValue placeholder="Select organization type" />
                                                            </SelectTrigger>
                                                        </FormControl>
                                                        <SelectContent>
                                                            {categories.map((categorie) => (
                                                                <SelectItem value={categorie.name}>{categorie.name}</SelectItem>
                                                            ))}
                                                        </SelectContent>
                                                    </Select>
                                                    <FormMessage />
                                                </FormItem>
                                            )}
                                        />
                                    </motion.div>
                                </AnimatePresence>

                                <motion.div
                                    className="grid md:grid-cols-2 gap-6"
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    transition={{ delay: 0.2 }}
                                >
                                    <FormField
                                        control={form.control}
                                        name="companyName"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-gray-700 font-medium">
                                                    {form.watch("organizerType") === "company"
                                                        ? "Company Name"
                                                        : "Department Name"}
                                                </FormLabel>
                                                <FormControl>
                                                    <div className="relative">
                                                        <Input
                                                            placeholder={
                                                                form.watch("organizerType") === "company"
                                                                    ? "Modern Tech Inc."
                                                                    : "Engineering Faculty"
                                                            }
                                                            {...field}
                                                            className="pl-10 h-12"
                                                        />
                                                        <Building2 className="absolute left-3 top-3.5 h-5 w-5 text-gray-400" />
                                                    </div>
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />

                                    <FormField
                                        control={form.control}
                                        name="contactPerson"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-gray-700 font-medium">Contact Person</FormLabel>
                                                <FormControl>
                                                    <Input
                                                        placeholder="John Doe"
                                                        {...field}
                                                        className="h-12"
                                                    />
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                </motion.div>

                                <motion.div
                                    className="grid md:grid-cols-2 gap-6"
                                    initial={{ opacity: 0 }} animate={{ opacity: 1 }}
                                    transition={{ delay: 0.3 }}
                                >
                                    <FormField
                                        control={form.control}
                                        name="email"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-gray-700 font-medium">Official Email</FormLabel>
                                                <FormControl>
                                                    <div className="relative">
                                                        <Input
                                                            placeholder="contact@company.com"
                                                            {...field}
                                                            className="pl-10 h-12"
                                                            disabled={emailCheckLoading}
                                                            onBlur={async (e) => {
                                                                if (e.target.value) {
                                                                    const exists = await checkEmailExists(e.target.value, "host");
                                                                    if (exists) {
                                                                        toast({
                                                                            title: "Email Already Registered",
                                                                            description: "This email is already in use. Please use a different email.",
                                                                            variant: "destructive",
                                                                            duration: 5000
                                                                        });
                                                                    }
                                                                }
                                                            }}
                                                        />
                                                        <Mail className="absolute left-3 top-3.5 h-5 w-5 text-gray-400" />
                                                        {emailCheckLoading && (
                                                            <CircleDashed className="absolute right-3 top-3.5 h-5 w-5 text-gray-400 animate-spin" />
                                                        )}
                                                    </div>
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />

                                    <FormField
                                        control={form.control}
                                        name="phone"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-gray-700 font-medium">Phone Number</FormLabel>
                                                <FormControl>
                                                    <div className="relative">
                                                        <Input
                                                            placeholder="1234567890"
                                                            {...field}
                                                            className="pl-10 h-12"
                                                        />
                                                        <Phone className="absolute left-3 top-3.5 h-5 w-5 text-gray-400" />
                                                    </div>
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                </motion.div>

                                <motion.div
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    transition={{ delay: 0.4 }}
                                >
                                    <FormField
                                        control={form.control}
                                        name="address"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-gray-700 font-medium">Main Address</FormLabel>
                                                <FormControl>
                                                    <div className="relative">
                                                        <Input
                                                            placeholder="123 Main St, City"
                                                            {...field}
                                                            className="pl-10 h-12"
                                                        />
                                                        <MapPin className="absolute left-3 top-3.5 h-5 w-5 text-gray-400" />
                                                    </div>
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                </motion.div>

                                <motion.div
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    transition={{ delay: 0.5 }}
                                >
                                    <FormField
                                        control={form.control}
                                        name="website"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-gray-700 font-medium">Website</FormLabel>
                                                <FormControl>
                                                    <div className="relative">
                                                        <Input
                                                            placeholder="https://www.company.com"
                                                            {...field}
                                                            className="pl-10 h-12"
                                                        />
                                                        <Link className="absolute left-3 top-3.5 h-5 w-5 text-gray-400" />
                                                    </div>
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                </motion.div>

                                <motion.div
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    transition={{ delay: 0.6 }}
                                >
                                    <FormField
                                        control={form.control}
                                        name="document"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-gray-700 font-medium">Authorization Document</FormLabel>
                                                <FormControl>
                                                    <div className="flex items-center gap-4">
                                                        <label className={cn(
                                                            "flex-1 cursor-pointer border-2 border-dashed rounded-lg p-4 h-24 flex flex-col items-center justify-center",
                                                            "hover:border-blue-400 transition-colors",
                                                            field.value ? "border-blue-300 bg-blue-50" : "border-gray-300"
                                                        )}>
                                                            <FileText className="h-6 w-6 text-gray-500 mb-2" />
                                                            <span className="text-sm text-gray-600">
                                                                {field.value?.name || "Click to upload document"}
                                                            </span>
                                                            <input
                                                                type="file"
                                                                accept=".pdf,.docx"
                                                                onChange={(e) => field.onChange(e.target.files?.[0])}
                                                                className="hidden"
                                                            />
                                                        </label>
                                                    </div>
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                </motion.div>

                                <motion.div
                                    className="pt-4"
                                    initial={{ opacity: 0, y: 20 }}
                                    animate={{ opacity: 1, y: 0 }}
                                    whileHover={{ scale: 1.02 }}
                                    whileTap={{ scale: 0.98 }}
                                    transition={{ delay: 0.7 }}
                                >
                                    <Button
                                        type="submit"
                                        className="w-full h-14 text-lg font-medium"
                                        disabled={isSubmitting || emailCheckLoading}
                                    >
                                        {isSubmitting || emailCheckLoading ? (
                                            <>
                                                <CircleDashed className="h-5 w-5 mr-2 animate-spin" />
                                                {emailCheckLoading ? "Checking email..." : "Processing..."}
                                            </>
                                        ) : (
                                            <>
                                                Complete Registration
                                                <ArrowRight className="h-5 w-5 ml-2" />
                                            </>
                                        )}
                                    </Button>
                                </motion.div>

                                <motion.div
                                    className="mt-8 border-t pt-6 text-center"
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    transition={{ delay: 0.8 }}
                                >
                                    <p className="text-sm text-gray-600">
                                        By continuing, you agree to our{" "}
                                        <a href="#" className="text-blue-600 hover:underline font-medium">
                                            Terms of Service
                                        </a>{" "}
                                        and{" "}
                                        <a href="#" className="text-blue-600 hover:underline font-medium">
                                            Privacy Policy
                                        </a>
                                    </p>
                                </motion.div>
                            </form>
                        </Form>
                    </div>
                </div>
            </motion.div >
        </div >
    );
};

export default OrganizerRegisterForm;